<template>
    <article class="verification_phone">
        <van-cell-group :border="false">
        <van-field
                type="tel"
                label=""
                v-model="tel"
                placeholder=""
                :border="true"
        />
        <div class="tel">
            <van-field
                    center
                    clearable
                    v-model="config.sms"
                    label=""
                    placeholder=""
            >
                <van-button
                        slot="button"
                        size="small"
                        type="primary"
                        @click="send"
                        :disabled="flag"
                >{{info}}</van-button>
            </van-field>
        </div>
     </van-cell-group>
    </article>
</template>

<script>
    export default {
        name: "verificationPhone",
        data() {
            return {
                tel:'',
                info:'',
                flag:false,
            }
        },
        props:{
            config:{
                type:Object
            }
        },
        methods:{
            send() {
                this.info = 0
                this.flag = !this.flag
                let id = setInterval(() => {
                    this.info ++
                    if(this.info >= 60) {
                        window.clearInterval(id)
                        this.info = ''
                        this.flag = !this.flag
                    }
                },1000)
            },
        },
    }
</script>

