<template>
<article class="changes_address">
    <van-popup
            v-model="config.show"
            position="bottom"
            custom-style="height: 70%"
    >
        <van-area
                :area-list="areaList"
                :columns-placeholder=" ['', '', '']"
                title=""
                @cancel="close"
                @confirm="confirm"
        />
    </van-popup>
</article>
</template>

<script>
    export default {
        name: "changeAddress",
        props:{
            config:Object,
        },
        data() {
          return {
              areaList:{
                  province_list: {
                      110000: '',
                      120000: ''
                  },
                  city_list: {
                      110100: '',
                      110200: '',
                      120100: '',
                      120200: ''
                  },
                  county_list: {
                      110101: '',
                      110102: '',
                      110105: '',
                      110106: '',
                      120101: '',
                      120102: '',
                      120103: '',
                      120104: '',
                      120105: '',

                  }
              }
          }
        },
        methods:{
            close() {
               this.config.show = !this.config.show
            },
            confirm(v) {
                this.config.show = !this.config.show
                this.$emit(`getAddr`,v)
            }
        },
    }
</script>

